import uniTransition from '@/components/uni-transition/uni-transition.vue';
import viewHighlight from '@/components/view-highlight.vue';
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';

import { mapState } from 'vuex';

export default {
	components: { uniTransition, viewHighlight, uniSegmentedControl },
	name: 'icon-picker',
	props: {
		tabItems: {
			type: Array
		},
		iconSets: {
			type: Array
		},
		defIcon: {
			type: String,
			default: 'transparent'
		},
		w: {
			type: Number,
			default: 50
		},
		h: {
			type: Number,
			default: 50
		}
	},
	data() {
		return {
			// tabItems:['a','b','c'],
			showPicker: false,
			icon: '',
			style: '',
			hl: false,
			tabIndex: 0,
			transfromClass: {
				position: 'absolute',
				bottom: 0,
				top: 0,
				left: 0,
				right: 0,
				width: '100%',
				height: '100%',
				margin: 'auto',
				display: 'flex',
				'justify-content': 'center',
				'align-items': 'center',
				'background-color': 'rgba(251, 251, 251, 0.9)'
			}
		};
	},
	methods: {
		getIcon(e) {
			// console.log(JSON.stringify(e));
			this.icon = e.currentTarget.dataset.icon;
			console.log(this.icon);
		},
		onChangeColorSetTabItem: function(e) {
			if (this.tabIndex !== e.currentIndex) {
				this.tabIndex = e.currentIndex;
			}
		},
		transIsDone() {},
		onDone() {},
		onChangeTabItem(e) {
			if (this.tabIndex !== e.currentIndex) {
				this.tabIndex = e.currentIndex;
			}
		}
	},
	mounted() {
		this.icon = this.defIcon;
		console.log('icon-picker.onLoad' + this.icon)
	}
};
